@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');
@charset "UTF-8";
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  height: 100%;
  width: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-abjust: 100%;
  -moz-text-size-abjust: 100%;
  -webkit-text-size-abjust: 100%;
}

input, buttom, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

/*убирает крестик в поле ввода в exploer*/
button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

/*убирает лишние отступы и пробелы*/
a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}

.body{
}

.wrapper{
  min-height: 100%;
  overflow: hidden;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 400;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  position: relative;

}


.container	{
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}
.content{
  flex: 1 1 auto;
}

header{

  margin: 20px 0;
  .header__row{
    display: flex;
    justify-content: space-between;
    .header__row-logo{
      display: flex;
      align-items: center;
      .header__row_logo-text a{
        font-size: 28px;
        font-weight: 900;
        text-decoration: none;

        margin: 0 5px 0 0;
      }
      .header__row_logo-img{}
    }
    .header__row-nav{
      .header__list{
        display: flex;
        align-items: center;
        .header__link a{
          font-weight: 500;
          font-size: 16px;
          margin: 0 20px;
          text-transform: uppercase;
          text-decoration: none;
          color: #000;

          &:hover{
            color: #7FAD39;
            transition: 0.1s linear;
          }
        }
      }
    }
    .header__row-item{
      display: flex;
      align-items: center;
      .header__row-item-img img{
        margin: 0 4px;
      }
      .header__row-item-cart{
        display: flex;
        .sign a{
          font-size: 15px;
          color: aliceblue;
          border-radius: 30px;
          font-weight: bold;
          margin: 0 10px 0 20px;
          background: #7FAD39;
          text-decoration: none;
          padding: 5px 10px;
        }
        .log a{
          font-size: 15px;
          color: aliceblue;
          border-radius: 30px;
          font-weight: bold;
          margin: 0 10px 0 0;
          background: #7FAD39;
          padding: 5px 10px;
          text-decoration: none;
        }
      }
    }
  }
}

/*-----------------------------------------------*/

.search{
  margin: 0 0 20px 0;
  .search__row{
    display: flex;

    align-items: center;

    .select{
      position: relative;
      width: 270px;
      display: flex;
      height: 50px;
      margin: 0 20px 0 0;


      .select__header{
        border: 1px solid #ccc;
        background-color: #7FAD39;
        cursor: pointer;
        display: flex;
        justify-content: space-around;
        align-items: center;

        .selectnav__icon{
          align-items: center;
          display: flex;
          flex-shrink: 0;
          justify-content: center;
          height: 40px;
          margin: 19px 0 19px 20px;
          text-align: center;

        }

        .select__current{
          font-size: 16px;
          line-height: 19px;
          font-weight: 800;
          color: #fff;
          padding: 19px 45px 19px 23px;
        }

        .select__icon{
          align-items: center;
          display: flex;
          flex-shrink: 0;
          justify-content: center;
          height: 40px;
          margin: 12px 26px 12px 0;
          text-align: center;

        }
      }
      .select__body{
        border: 1px solid #cccccc;

        border-top: 0;
        display: none;
        left: 0;
        position: absolute;
        right: 0;
        top: 100%;
        background-color: #fff;
        .select__item{
          cursor: pointer;
          font-size: 16px;
          line-height: 24px;
          padding: 8px;
          &:hover{
            background-color: #f2f2f2;
          }

        }
      }
    }

    .select.is-active .select__body {
      display: block;
    }

    .search__row-window{
      display: flex;
      border: 1px solid #2A2A2A33;
      width: 621px;
      height: 51px;
      margin: 0 56px 0 0;
      .search__row-window-one{
        .select{
          position: relative;
          width: 193px;
          display: flex;
          height: 50px;


          .select__header{
            background-color: #fff;
            cursor: pointer;
            display: flex;
            justify-content: space-around;
            align-items: center;


            .select__current{
              font-size: 16px;
              line-height: 16px;
              font-weight: 800;
              color: #000;
              padding: 19px 45px 19px 19px;
            }

            .select__icon{
              align-items: center;
              display: flex;
              flex-shrink: 0;
              justify-content: center;
              height: 40px;
              margin: 12px 22px 12px 0;
              text-align: center;

            }
          }
          .select__body{
            border: 1px solid #cccccc;

            border-top: 0;
            display: none;
            left: 0;
            position: absolute;
            right: 0;
            top: 100%;
            .select__item{
              cursor: pointer;
              font-size: 16px;
              line-height: 24px;
              padding: 8px;
              &:hover{
                background-color: #f2f2f2;
              }

            }
          }
        }

        .select.is-active .select__body {
          display: block;
        }

      }

      .search__row-window-two{
        width: 322px;
        display: flex;
        align-items: center
      }

      .search__row-button{
        display: flex;
        align-items: center;
        .batton{
          background: #7FAD39;
          font-weight: bold;
          font-size: 16px;
          letter-spacing: 0.1em;
          color: #ffffff ;
          text-decoration: none;
          padding: 17px 19px;

          &:hover{
            background-color: #70913f;
            transition: 0.1s linear;
          }


        }
      }
    }
    .search__row-number{
      display: flex;
      .search__row-number_img{
        margin: 0 20px 0 0;
      }
      .search__row-number_num{
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 16px;
        font-weight: 900;
        line-height: 20px;


      }
      .search__row-number_num span{
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 12px;
        font-weight: 400;
        color: #3b3b3b33;
      }
    }
  }
}

