/*--------------------------------------------------------------------------------*/

.product{
  margin: 0 0 50px 0;
  .product__column{
    .product__column-title{
      font-weight: 900;
      font-size: 34px;
      text-align: center;
    }
    .product__column-categories{
      margin: 50px 0 50px 0;
      .categor__list{
        display: flex;
        justify-content: center;
        .categor__link a{
          margin: 0 10px;
          font-weight: 500;
          font-size: 16px;
          text-decoration: none;
          color: #999999;

          &:hover{
            color: #000;
            border-bottom: 3px solid #000;
            transition: 0.1s linear;
          }
        }
      }
    }
  }
  .product__net{
    display: grid;
    grid-template-rows: 356px 356px;
    grid-template-columns: 270px 270px 270px 270px;
    grid-gap: 20px;
    .product__net-cart{
      background-color: #f2f2f2;
      .product__net-cart-img{
        margin: 20px 15px 0 15px;
      }
      .product__net-cart-price{
        width: 230px;
        height: 80px;
        background-color: #fff;
        margin: 60px 20px 20px 20px;
        .title{
          color: #2A2A2A;
          font-size: 15px;
          margin: 0 0 10px 0;
          text-align: center;
        }
        .price{
          color: #2A2A2A;
          font-size: 18px;
          font-weight: bold;
          text-align: center;
        }
      }
    }

  }
}

